<template>
  <div
    class="modal fade"
    id="modalid"
    data-backdrop="static"
    data-keyboard="false"
    tabindex="-1"
    aria-labelledby="staticBackdropLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h5
            class="modal-title"
            id="staticBackdropLabel"
            v-if="getterdate.length !== 0"
          >
            {{ getterdate[modalid].date }}
          </h5>
          <button
            type="button"
            class="close"
            data-dismiss="modal"
            aria-label="Close"
          >
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-6">
              <div class="form-group">
                <label for="exampleInputEmail1">เวลาเข้า {{ start }}</label>
                <input
                  type="time"
                  class="form-control"
                  aria-describedby="emailHelp"
                  v-model="start"
                />
              </div>
            </div>
            <!-- ------------------------ -->
            <div class="col-6">
              <div class="form-group">
                <label for="exampleInputEmail1">เวลาออก {{ end }}</label>
                <input
                  type="time"
                  class="form-control"
                  aria-describedby="emailHelp"
                  v-model="end"
                />
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-danger" data-dismiss="modal">
            ปิด
          </button>
          <button type="button" class="btn btn-info" @click="confirm()">
            ยืนยัน
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "editdate",
  props: ["modalid"],
  data() {
    return {
      getterdate: [],
      start: "",
      end: "",
    };
  },
  mounted() {
    this.start = "";
    this.end = "";
  },
  watch: {
    date(value) {
      console.log(value);
      this.getterdate = value;
    },
  },
  computed: {
    date() {
      return this.$store.getters["homeActions/GET_data"];
    },
  },
  methods: {
    confirm() {
      try {
        if (this.start == "" || this.end == "") {
          throw "กรอกข้อมูลให้ครบถ้วน";
        }
        let data = { index: this.modalid, start: this.start, end: this.end };
        this.$store.dispatch("homeActions/add", data);
        this.$$(`#modalid`).modal("hide");
      } catch (error) {
        alert(error);
      }
    },
  },
};
</script>

