import Vue from 'vue'
import Vuex from 'vuex'
import { loginActions } from '../store/actions/loginActions'
import { loadingActions } from '../store/actions/loadingActions'
import { positionActions } from '../store/actions/positionActions'
import { employeeActions } from '../store/actions/employeeActions'
import { homeActions } from '../store/actions/homeActions'
Vue.use(Vuex)

export default new Vuex.Store({
  modules: {
    loginActions: loginActions,
    loadingActions: loadingActions,
    positionActions: positionActions,
    employeeActions: employeeActions,
    homeActions: homeActions

  }
})
