<template>
  <div class="row w-100 mx-0 mt-3 p-0 bfont">
    <div class="col-12">
      <div class="card">
        <div class="row w-100 m-0 p-2 my-3">
          <!-- -------------------- -->
          <div class="col-4">
            <div class="form-group">
              <label for="exampleInputEmail1">พนักงาน</label>
              <select
                id="inputState"
                class="form-control"
                v-model="user.emp"
                @change="gen()"
              >
                <option value="">เลือกพนักงาน...</option>
                <option
                  v-for="(e, i) in $store.getters['employeeActions/GET_data']"
                  :key="i"
                  :value="e._id"
                >
                  {{ e.employees_name }}
                </option>
              </select>
            </div>
          </div>
          <!-- -------------------- -->
          <div class="col-4">
            <div class="form-group">
              <label for="exampleInputEmail1">เริ่มวันที่</label>
              <input
                type="date"
                class="form-control"
                aria-describedby="emailHelp"
                v-model="user.start_date"
                @change="gen()"
              />
            </div>
          </div>
          <!-- -------------------- -->
          <div class="col-4">
            <div class="form-group">
              <label for="exampleInputEmail1">ถึงวันที่</label>
              <input
                type="date"
                class="form-control"
                aria-describedby="emailHelp"
                v-model="user.end_date"
                @change="gen()"
              />
            </div>
          </div>
          <!-- -------------------- -->
        </div>
        <div class="row m-0 p-0" id="printJS-form" ref="pdf">
          <div class="col-12 p-2">
            <div class="card shadow rounded">
              <div
                class="row w-100 m-0 px-2 py-3 bg-info text-white"
                v-if="getter.length !== 0"
              >
                <div class="col-6">
                  ชื่อ : {{ getter.employee.employees_name }}
                </div>
                <!-- ------------------------ -->
                <div class="col-6">
                  ตำเเหน่ง : {{ getter.positions.position_name }}
                </div>
                <!-- --------------------------- -->
                <div class="col-6">
                  ค่าจ้าง (เต็มวัน/ครึ่งวัน) :
                  {{ getter.employee.pay_full_time }}/{{
                    getter.employee.pay_half_time
                  }}
                  บาท
                </div>
                <!-- --------------------------- -->
                <div class="col-6">
                  หัก (กรณีเข้าสาย) :
                  {{ getter.employee.cut_late_for_work }} บาท/ชม.
                </div>
                <!-- --------------------------- -->
                <!-- --------------------------- -->
                <div class="col-6">
                  เข้างานเต็มวัน : {{ getter.positions.full_time_in }}-{{
                    getter.positions.full_time_late
                  }}/{{ getter.positions.full_time_out }}
                </div>
                <!-- --------------------------- -->
                <!-- --------------------------- -->
                <div class="col-6">
                  เข้างานครึ่งวัน : {{ getter.positions.half_time_in }}-{{
                    getter.positions.half_time_late
                  }}/{{ getter.positions.full_time_out }}
                </div>
                <!-- --------------------------- -->
              </div>
              <!-- ------- date -------------------- -->
              <!-- {{ getterdate }} -->
              <div class="m-0 px-2 w-100" v-if="getterdate.length > 0">
                <!-- <div class="row m-0 p-3" v-for="(e, i) in getterdate" :key="i">
                  <div class="col-3">{{ e.date }}</div>
                  <div class="col-3">{{ e.start }}</div>
                  <div class="col-3">{{ e.end }}</div>
                  <div class="col-3">
                    <p>
                      <span v-html="iconedit" @click="callmodal(i)"></span
                      >&nbsp;&nbsp;&nbsp;<span
                        v-html="iconremove"
                        @click="remove(i)"
                      ></span>
                    </p>
                  </div>

                </div> -->

                <table class="table table-hover mt-3 shadow bg-white rounded">
                  <thead>
                    <tr>
                      <th scope="col">วันที่</th>
                      <th scope="col">เวลาเข้า</th>
                      <th scope="col">เวลาออก</th>
                      <th scope="col">หัก(บาท)</th>
                      <th scope="col">ชม.ทำงาน</th>
                      <th scope="col">ค่าจ้าง(บาท)</th>
                      <th scope="col">Action</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(e, i) in getterdate" :key="i">
                      <td>{{ e.date }}</td>
                      <td>
                        <input
                          v-if="index == i"
                          type="time"
                          class="form-control"
                          v-model="start"
                        />
                        <p v-else>{{ e.start }}</p>
                      </td>
                      <td>
                        <input
                          v-if="index == i"
                          type="time"
                          class="form-control"
                          v-model="end"
                        />
                        <p v-else>{{ e.end }}</p>
                      </td>
                      <td>{{ e.cut }}</td>
                      <td>{{ e.hour }}</td>
                      <td>{{ e.pay }}</td>
                      <td>
                        <p>
                          <span
                            v-html="iconedit"
                            @click="edit(i, e)"
                            v-if="index != i"
                          ></span>
                          <span
                            v-html="icondone"
                            @click="confirm(i)"
                            v-else-if="index == i"
                          ></span>
                          &nbsp;&nbsp;&nbsp;<span
                            v-html="iconremove"
                            @click="remove(i)"
                          ></span>
                        </p>
                      </td>
                    </tr>
                    <tr>
                      <th scope="col">รวม</th>
                      <th scope="col"></th>
                      <th scope="col"></th>
                      <th scope="col">{{ cut }}</th>
                      <th scope="col"></th>
                      <th scope="col">{{ pay }}</th>
                      <th scope="col"></th>
                    </tr>
                  </tbody>
                </table>

                <!-- ------------------------------- -->
                <p class="mainfont">ค่าจ้างพิเศษ</p>
                <table class="table table-hover mt-3 shadow bg-white rounded">
                  <thead>
                    <tr>
                      <th scope="col">รายการ</th>
                      <th scope="col">ค่าจ้าง</th>
                      <th scope="col">Action</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(e, i) in jobother" :key="i">
                      <td>
                        <input
                          v-if="index_g == i"
                          type="time"
                          class="form-control"
                          v-model="detail_g"
                        />
                        <p v-else>{{ e.name }}</p>
                      </td>
                      <td>
                        <input
                          v-if="index_g == i"
                          type="time"
                          class="form-control"
                          v-model="payjob_g"
                        />
                        <p v-else>{{ e.pay }}</p>
                      </td>

                      <td>
                        <p>
                          &nbsp;&nbsp;&nbsp;<span
                            v-html="iconremove"
                            @click="remove_g(i)"
                          ></span>
                        </p>
                      </td>
                    </tr>
                    <tr>
                      <th scope="col">
                        <input
                          type="taxt"
                          class="form-control"
                          v-model="detail"
                        />
                      </th>

                      <th scope="col">
                        <input
                          type="number"
                          class="form-control"
                          v-model="payjob"
                        />
                      </th>

                      <th scope="col">
                        <button
                          type="button"
                          class="btn btn-info"
                          @click="addjobther()"
                        >
                          เพิ่ม
                        </button>
                      </th>
                    </tr>
                    <tr>
                      <th scope="col">รวม</th>

                      <th scope="col">{{ pay_g }}</th>

                      <th scope="col"></th>
                    </tr>
                  </tbody>
                </table>
                <!-- ------------------------------- -->
                <p class="mainfont">รายการเบิก</p>
                <table class="table table-hover mt-3 shadow bg-white rounded">
                  <thead>
                    <tr>
                      <th scope="col">รายการ</th>
                      <th scope="col">เบิก</th>
                      <th scope="col">Action</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(e, i) in paybefore" :key="i">
                      <td>
                        <input
                          v-if="index_p == i"
                          type="time"
                          class="form-control"
                          v-model="detail_p"
                        />
                        <p v-else>{{ e.name }}</p>
                      </td>
                      <td>
                        <input
                          v-if="index_p == i"
                          type="time"
                          class="form-control"
                          v-model="payjob_p"
                        />
                        <p v-else>{{ e.pay }}</p>
                      </td>

                      <td>
                        <p>
                          &nbsp;&nbsp;&nbsp;<span
                            v-html="iconremove"
                            @click="remove_paybefore(i)"
                          ></span>
                        </p>
                      </td>
                    </tr>
                    <tr>
                      <th scope="col">
                        <input
                          type="taxt"
                          class="form-control"
                          v-model="detailb"
                        />
                      </th>

                      <th scope="col">
                        <input
                          type="number"
                          class="form-control"
                          v-model="payb"
                        />
                      </th>

                      <th scope="col">
                        <button
                          type="button"
                          class="btn btn-info"
                          @click="addpaybefore()"
                        >
                          เพิ่ม
                        </button>
                      </th>
                    </tr>
                    <tr>
                      <th scope="col">รวม</th>

                      <th scope="col">{{ pay_p }}</th>

                      <th scope="col"></th>
                    </tr>
                  </tbody>
                </table>
              </div>
              <button
                type="button"
                class="btn btn-info"
                v-if="getterdate.length > 0"
                @click="cal()"
              >
                คำนวณ
              </button>
            </div>
          </div>
        </div>
        <!-- ------------------------ -->
      </div>
    </div>
    <!-- <Modaledit :key="remodal" :modalid="modalid" /> -->
  </div>
</template>

<script>
import Modaledit from "@/components/Home/Modaledit.vue";
import printJS from "print-js";
import jspdf from "jspdf";
import html2pdf from "html2pdf.js";
import hmtl from "../services/hmtl";
import font from "../fonts/Prompt-Thin.ttf";
export default {
  name: "Home",
  components: { Modaledit },
  data() {
    return {
      index: -100,
      index_g: -100,
      start: "",
      end: "",
      cut: 0,
      pay: 0,
      pay_g: 0,
      pay_p: 0,
      modalid: "",
      remodal: "",
      detail: "",
      payjob: "",
      payb: "",
      detalib: "",
      detail_g: "",
      payjob_g: "",
      iconedit: `<svg xmlns="http://www.w3.org/2000/svg" width="25" height="25" fill="#17a2b8" class="bi bi-pencil-square" viewBox="0 0 16 16">
  <path d="M15.502 1.94a.5.5 0 0 1 0 .706L14.459 3.69l-2-2L13.502.646a.5.5 0 0 1 .707 0l1.293 1.293zm-1.75 2.456-2-2L4.939 9.21a.5.5 0 0 0-.121.196l-.805 2.414a.25.25 0 0 0 .316.316l2.414-.805a.5.5 0 0 0 .196-.12l6.813-6.814z"/>
  <path fill-rule="evenodd" d="M1 13.5A1.5 1.5 0 0 0 2.5 15h11a1.5 1.5 0 0 0 1.5-1.5v-6a.5.5 0 0 0-1 0v6a.5.5 0 0 1-.5.5h-11a.5.5 0 0 1-.5-.5v-11a.5.5 0 0 1 .5-.5H9a.5.5 0 0 0 0-1H2.5A1.5 1.5 0 0 0 1 2.5v11z"/>
</svg>`,
      icondone: `<svg xmlns="http://www.w3.org/2000/svg" width="25" height="25" fill="#20c997" class="bi bi-check-circle-fill" viewBox="0 0 16 16">
  <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zm-3.97-3.03a.75.75 0 0 0-1.08.022L7.477 9.417 5.384 7.323a.75.75 0 0 0-1.06 1.06L6.97 11.03a.75.75 0 0 0 1.079-.02l3.992-4.99a.75.75 0 0 0-.01-1.05z"/>
</svg>`,
      iconremove: `<svg xmlns="http://www.w3.org/2000/svg" width="25" height="25" fill="#dc3545" class="bi bi-trash" viewBox="0 0 16 16">
  <path d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6z"/>
  <path fill-rule="evenodd" d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1zM4.118 4 4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118zM2.5 3V2h11v1h-11z"/>
</svg>`,
      getter: [],
      getterdate: [],
      user: { emp: "", start_date: "", end_date: "" },
      jobother: [],
      paybefore: [],
    };
  },
  watch: {
    "user.emp": function () {
      this.user.start_date = "";
      this.user.end_date = "";
      this.$store.dispatch("homeActions/gen", []);
      //this.gen();
    },
    callgetter(value) {
      this.getter = value;
    },
    date(value) {
      return (this.getterdate = value);
    },
    getterdate: function () {
      let value = this.getterdate;
      this.pay = 0;
      this.cut = 0;
      let pay = 0;
      let cut = 0;
      for (var i in value) {
        pay = Number(pay) + Number(value[i].pay);
        cut = Number(cut) + Number(value[i].cut);
      }
      this.cut = cut;
      this.pay = pay;
    },
    getjobother(v) {
      return (this.jobother = v);
    },
    getpaybefore(v) {
      return (this.paybefore = v);
    },
    jobother: function () {
      let v = this.jobother;
      let pay = 0;
      for (var i in v) {
        // console.log(v[i].pay);
        pay = Number(pay) + Number(v[i].pay);
      }
      this.pay_g = pay;
    },
    paybefore: function () {
      let v = this.paybefore;
      let pay = 0;
      for (var i in v) {
        // console.log(v[i].pay);
        pay = Number(pay) + Number(v[i].pay);
      }
      this.pay_p = pay;
    },
  },
  computed: {
    callgetter() {
      return this.$store.getters["employeeActions/GET_employeeposition"];
    },
    date() {
      return this.$store.getters["homeActions/GET_data"];
    },
    getjobother() {
      return this.$store.getters["homeActions/GET_jobother"];
    },
    getpaybefore() {
      return this.$store.getters["homeActions/GET_paybefore"];
    },
  },
  mounted() {
    this.$store.dispatch("employeeActions/getdata");
    this.$store.dispatch("homeActions/get_jobother");
    this.$store.dispatch("homeActions/get_paybefore");
  },
  methods: {
    async addjobther() {
      try {
        if (this.payjob == "" || this.detail == "") {
          throw "กรอกข้อมูลให้ครบถ้วน";
        }
        let data = { name: this.detail, pay: this.payjob };
        this.$store.dispatch("homeActions/addorder", data);
        this.payjob = "";
        this.detail = "";
        this.jobother = this.$store.getters["homeActions/GET_jobother"];
      } catch (error) {
        alert(error);
      }
    },
    async addpaybefore() {
      try {
        if (this.payb == "" || this.detailb == "") {
          throw "กรอกข้อมูลให้ครบถ้วน";
        }
        let data = { name: this.detailb, pay: this.payb };
        this.$store.dispatch("homeActions/addorder_paybefore", data);
        this.payb = "";
        this.detailb = "";
        this.paybefore = this.$store.getters["homeActions/GET_paybefore"];
      } catch (error) {
        alert(error);
      }
    },
    async confirm(index) {
      try {
        if (this.start == "" || this.end == "") {
          throw "กรอกข้อมูลให้ครบถ้วน";
        }
        let data = { index: index, start: this.start, end: this.end };
        await this.$store.dispatch("homeActions/add", data);
        this.index = -100;
        this.getterdate = [];
        this.getterdate = await this.$store.getters["homeActions/GET_data"];
      } catch (error) {
        alert(error);
      }
    },
    edit(index, value) {
      this.index = index;
      // console.log(value);
      this.start = value.start;
      this.end = value.end;
    },

    async cal() {
      try {
        //  printJS('printJS-form', 'html')

        const html1 = await hmtl.genhtml(
          this.getter,
          this.getterdate,
          this.jobother,
          this.pay,
          this.cut,
          this.pay_g,
          this.paybefore,
          this.pay_p
        );

        await html2pdf(html1, {
          margin: 0.2,
          filename: `${this.getter.employee.employees_name}(${this.user.start_date}-${this.user.end_date}).pdf`,
          image: { type: "jpeg", quality: 0.98 },
          html2canvas: { dpi: 192, letterRendering: true },
          jsPDF: { unit: "in", format: "letter", orientation: "landscape" },
        });
      } catch (error) {
        alert(error);
      }
    },
    remove(index) {
      this.$store.dispatch("homeActions/remove", index);
    },
    remove_g(i) {
      //  let data = { name: v.name, pay: v.pay };
      this.$store.dispatch("homeActions/removeorder", i);
      this.jobother = this.$store.getters["homeActions/GET_jobother"];
    },
    remove_paybefore(i) {
      //  let data = { name: v.name, pay: v.pay };
      this.$store.dispatch("homeActions/removeorder_paybefore", i);
      this.paybefore = this.$store.getters["homeActions/GET_paybefore"];
    },
    async callmodal(index) {
      this.modalid = index;
      this.remodal = await Math.floor(Math.random() * 99);
      setTimeout(() => {
        this.$$(`#modalid`).modal("show");
      }, 1000);
    },
    gen() {
      if (this.user.emp !== "") {
        this.$store.dispatch("employeeActions/employeeposition", {
          id: this.user.emp,
        });
      } else if (this.user.emp == "") {
        this.$store.dispatch("employeeActions/addnull_employeeposition");
      }

      if (
        this.user.emp !== "" &&
        this.user.start_date !== "" &&
        this.user.end_date !== ""
      ) {
        if (new Date(this.user.start_date) > new Date(this.user.end_date)) {
          alert("ระบุช่วงวันไม่ถูกต้อง");
        } else {
          let data = { start: this.user.start_date, end: this.user.end_date };
          this.$store.dispatch("homeActions/gen", data);
        }
      }
    },
  },
};
</script>
