// import AuthService from '../services/auth.service';
import * as main from '../../services/mainservice';
import * as constant from '../../services/constant';
import * as serviceAPI from "../../services/APIservice";


export const loadingActions = {
    namespaced: true,
    state: { load: false },
    mutations: {
        MUT_Load(state) {
            state.load = true;
        },
        MUT_NLoad(state) {
            state.load = false;
        },


    },
    getters: {
        GET_LOAD(state) {
            return state.load;
        },

    },

    actions: {
        load({ commit }) {
            commit('MUT_Load')

        },
        nload({ commit }) {
            commit('MUT_NLoad')

        },
    },

};